import React from "react"
import { Link, graphql } from "gatsby"
import styled from 'styled-components';
import { scale, rhythm } from "../utils/typography"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"


const TimeToRead = styled.small`
  ${scale(-1/3)};
  padding-left: ${rhythm(.2)};
  color: darkgray;
`

const ArticleLink = styled(Link)`
  display: block;
  border-radius: 6px;
  background: #fff;
  box-shadow: 0 1px 1px 1px rgba(10,16,34,.08);
  color: #1b1b1b;
  padding: 2rem 2.4rem;
  margin-bottom: 3rem;
  transition: transform .2s ease,box-shadow .2s ease;
  will-change: transform,box-shadow;
  &:hover {
    box-shadow: 0 16px 32px 0 rgba(10,16,34,.2), 0 0 0 transparent;
    transform: translateY(-5px) translateZ(0);
    h3 {
      color: palevioletred;
    }
  };

  h3 {
    margin-top: 0.5rem;
    color: #007acc;
    transition: color .2s linear;
  }
  p {
    margin-bottom: 0.75rem;
  }
`

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO
          title="All posts"
          keywords={[`blog`, `gatsby`, `javascript`, `react`]}
        />
        <Bio />
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          return (
            <ArticleLink key={node.fields.slug} to={node.fields.slug}>
              <h3
                style={{
                  marginBottom: rhythm(1 / 4),
                }}
              >
                  {title}
              </h3>
              <small>{node.frontmatter.date}</small>{'.'} <TimeToRead>{node.frontmatter.timeToRead} min read</TimeToRead>
              <p
                dangerouslySetInnerHTML={{
                  __html: node.frontmatter.description || node.excerpt,
                }}
              />
            </ArticleLink>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            timeToRead
            description
          }
        }
      }
    }
  }
`
